@import "../common/mixins";
@import "../common/variable";

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  display: none;
  &__container {}
  &__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  &__navbar {}
}

.navbar-header {
  ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-gap: r(15);
    list-style: none;
    li {
      a {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        font-size: r(16);
        color: #ffffff;
        white-space: nowrap;
        -webkit-transition: 0.3s;
        transition: 0.3s;
        text-transform: uppercase;
        @include hover {
          color: #F44336;
        }
      }
    }
  }
}